import { T_Nullable } from "types/generic";

export enum E_MapControlActions {
  setGeometricInitials,
  pointerDown,
  pointerUp,
  pointerMove,
  zoomIn,
  zoomOut,
  zoomReset
}

export type T_ViewBoxProps = T_Coordinates & {
  width: number;
  height: number;
};

export type T_MapControlState = {
  zoomFactor: number;
  zoomCompensatoryTranslate: T_Coordinates;
  widthRatio: number;
  initialViewBox: T_ViewBoxProps;
  viewBox: T_ViewBoxProps;
  // for move events to check if pointer is down or not
  isPointerDown: boolean;
  // contains the original coordinates when the user start
  // pressing the mouse or touching the screen
  pointerOrigin: T_Coordinates;
  midPoint?: T_Coordinates;
  pointedAt: T_Nullable<T_PointedAt>;
};

export type T_Coordinates = { x: number; y: number };
export type T_GeometricInitials = Partial<{
  midPoint: T_Coordinates;
  widthRatio: number;
}>;

export type T_SVGEventType =
  | React.MouseEvent<SVGSVGElement, MouseEvent>
  | React.TouchEvent<SVGSVGElement>;

export type T_MapControlAction = [type: E_MapControlActions, payload?: any];
// TODO: Elaborate action types: VS code is able to understand them, but CRA is not
// export type T_MapControlAction =
//   | [
//       type: E_MapControlActions.setGeometricInitials,
//       payload: T_GeometricInitials
//     ]
//   | [type: E_MapControlActions.pointerDown, payload: T_Coordinates]
//   | [type: E_MapControlActions.pointerMove, payload: T_PointedAt]
//   | [type: E_MapControlActions.pointerUp]
//   | [type: E_MapControlActions.zoomIn]
//   | [type: E_MapControlActions.zoomOut]
//   | [type: E_MapControlActions.zoomReset];

export type T_PointedAt = {
  elementTitle?: string;
  position: T_Coordinates;
};
